.kitchen-div{
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	align-items: center;
	height: 80vh;
	width: 100vw;
	font-family: 'Codec Pro';
}
h2{
	padding-bottom: 1rem;
	font-size: 2rem;
}

.kbutton{
	display: block;
	width: 100%;
	margin-top: 1rem;
	padding: .5rem;
	background-color: white;
	box-shadow: 0 20px 30px 0 rgba(0,0,0,.1),
				0 4px 4px 0 rgba(0,0,0,0.2);
	border-style: none;
}

Link{
	text-decoration: none;
}

.kbutton:hover{
	background-color: black;
	color: white;
}

.kinput{
	width: 100%;
	text-align: center;
	border-style: none;
	border-bottom: 2px solid rgba(0,0,0,0.2);
}


.Kitchen-selector{
	height: 290px;
	width: 310px;
	box-shadow: 0 20px 30px 0 rgba(0,0,0,.1),
				0 4px 4px 0 rgba(0,0,0,0.3);
	padding: 2rem;
}